/*-----------------------------------------------
|   Select 2
-----------------------------------------------*/
@import '../../../node_modules/select2/src/scss/core.scss';
@import '../../../node_modules/@ttskch/select2-bootstrap4-theme/src/layout.scss';

.select2-container {
  .select2-selection--single .select2-selection__rendered {
    padding-left: $input-padding-x;
    padding-right: $input-padding-x;
  }
}

.select2-container--bootstrap4 {
  .select2-selection--multiple {
    .select2-selection__rendered {
      padding-left: $input-padding-x;
      padding-right: $input-padding-x;
      vertical-align: top;
    }
    .select2-selection__choice {
      border-color: $gray-200;
      margin-top: 5px;
      font-size: 15px;
      background-color: $gray-100;
    }
  }
  .select2-selection--single .select2-selection__arrow {
    right: 11px;
  }
}

.select2-search--dropdown {
  padding: 0.5rem $input-padding-x;
  .select2-search__field {
    padding: 0.25rem 0.5rem;
  }
}

.select2-results__options {
  .select2-results__option {
    padding-left: $input-padding-x;
    padding-right: $input-padding-x;
  }
}

.select2-container .select2-search--inline {
  vertical-align: top;
  line-height: 24px;
}

.select2-container--bootstrap4
  .select2-selection--multiple
  .select2-selection__rendered
  .select2-search__field {
  line-height: 22px;
  vertical-align: middle;
}

.select2-dropdown.select2-dropdown--below {
  border-top: 1px solid $gray-200 !important;
}

.select2-container--bootstrap4.select2-container--focus.select2-container--open
  .select2-selection {
  border-bottom: 1px solid transparent;
}

.select2-container {
  .select2-selection.multi-input {
    .select2-selection__choice {
      float: none;
    }
    .select2-search {
      float: none;
      margin-bottom: 5px;
    }
  }

  &.select2-container--focus.select2-container--open {
    .select2-selection.multi-input {
      border-bottom: $input-border-width solid $input-border-color;
      border-bottom-left-radius: $input-border-radius;
      border-bottom-right-radius: $input-border-radius;
    }
  }

  .select2-dropdown.multi-input {
    display: none;;
  }
}

select[readonly].select2 + .select2-container {
  pointer-events: none;
  touch-action: none;

  .select2-selection {
    background: $input-disabled-bg;
    box-shadow: none;
  }

  .select2-selection__arrow,
  .select2-selection__clear {
    display: none;
  }
}

// Validation

@each $state, $data in $form-validation-states {
  $color: map-get($data, color);
  $icon: map-get($data, icon);
  
  .select2-selection--single {
    @include form-validation-state-selector($state) {
      border-color: $color;

      @if $enable-validation-icons {
        padding-right: $input-height-inner;
        background-image: escape-svg($icon);
        background-repeat: no-repeat;
        background-position: right $input-height-inner-quarter center;
        background-size: $input-height-inner-half $input-height-inner-half;
      }

      &:focus {
        border-color: $color;
        box-shadow: 0 0 0 $input-focus-width rgba($color, .25);
      }
    }
  }
}
