/*-----------------------------------------------
|   Data table
-----------------------------------------------*/

.dataTables_wrapper {
  .table-processing {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 2000;

    &-content {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: rgba($black, .10);
    }
  }

  .dt-buttons {
    margin: 0 map-get($spacers, 3) map-get($spacers, 3);
    display: flex;

    .btn {
      margin-left: map-get($spacers, 2);

      &.buttons-colvis {
        margin-left: 0;
        margin-right: auto;
        order: -1;
      }
    }
  }

  .dataTable {
    margin-top: 0 !important;
    margin-bottom: 0 !important;
    table-layout: fixed;
  
    font-size: map-get($font-sizes, '-1');
  
    .btn-details {
      color: $link-color;
      
      &:focus:not(.active),
      &:hover:not(.active) {
        box-shadow: $btn-reveal-hover-shadow;
      }
    
      &.active {
        box-shadow: none;
        background-color: darken($white, 10%);
        border-color: darken($white, 12.5%);
        color: $link-hover-color;
      }
    }
  
    .btn-actions {
      color: $dark;
      
      &:hover:not(:focus) {
        box-shadow: $btn-reveal-hover-shadow;
      }
    
      &:active:focus,
      &:active,
      &:focus {
        box-shadow: none;
        background-color: darken($white, 10%);
        border-color: darken($white, 12.5%);
      }
    }

    th {
      font-weight: $font-weight-semi-bold;
      vertical-align: top;
    }

    tr {
      &.deactivated {
        td:first-child {
          box-shadow: inset $spacer*0.125 0px 0px 0px rgba($warning, 0.5);
        }
      }
    }
  
    td {
      vertical-align: middle;

      hr {
        margin-top: map-get($spacers, 2);
        margin-bottom: map-get($spacers, 2);
      }

      &.deactivated {
        box-shadow: inset $spacer*0.125 0px 0px 0px rgba($warning, 0.5);
      }

      &.details {
        padding: 0 !important;
        border-left: $card-spacer-x solid $gray-200;
      }
    }
  }

  .paging_full_numbers,
  .paging_first_last_numbers,
  .paging_full,
  .paging_numbers,
  .paging_simple_numbers {
    .page-link {
      @extend .btn;
      @extend .btn-falcon-default;
      margin-left: map-get($spacers, 1);
      margin-right: map-get($spacers, 1);
      padding: 3px 12px;
      border-radius: $border-radius !important;
      border: 1px solid $white !important;
    }

    .page-item {
      &.active {
        .page-link {
          background-color: transparent;
          color: $primary !important;
        }
      }

      &.disabled {
        .page-link {
          background-color: $light !important;
        }
      }
    }
  }

  .dataTables_length {
    label {
      margin: 0;
    }
  }

  .dataTables_scrollHead,
  .dataTables_scrollBody {
      width: 100% !important;
  }
}



