.spinner-border{
  animation-duration: 0.85s;
  animation-timing-function: cubic-bezier(0.47, 0.25, 0.4, 0.65);
}

.global-spinner {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2000;
  background-color: rgba($black, .10);
}
